@import url(https://fonts.googleapis.com/css2?family=Karla:wght@300;400&family=Rubik&display=swap);
:root {
  --light: #faf0f1;
  --dark: #373e40;
  --primary: #488286;
  --secondary: #305252;
  --main-font: "Karla", sans-serif;
  --second-font: "Rubik", sans-serif;
}

body {
  font-family: "Karla", sans-serif;
  font-family: var(--main-font);
  font-weight: 300;
  background-color: #faf0f1;
  background-color: var(--light);
  text-align: center;
}

.footer {
  background-color: black;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
}

.head {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.timedate {
  width: 50%;
  justify-self: flex-start;
}

.theme-btns {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-right: 5px;
  margin-top: 5px;
  width: 50%;
}

.light {
  width: 18px;
  height: 18px;
  padding: 15px;
  cursor: pointer;
}

.dark {
  width: 18px;
  height: 18px;
  padding: 15px;
  cursor: pointer;
}

.forest {
  width: 18px;
  height: 18px;
  padding: 15px;
  cursor: pointer;
}

.todo-input {
  padding: 10px;
  font-size: 18px;
  border-width: 3px;
  border-color: #152821;
  background-color: var(--light);
  color: #000000;
  border-style: solid;
  border-radius: 23px;
  width: 350px;
}

.todo-input:focus {
  outline: none;
}

.todo-form {
  display: flex;
  justify-content: center;
}

.todo-btn {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  background: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.add-btn {
  width: 20px;
  height: 20px;
  margin-top: 5px;
  padding-left: 5px;
}


.list {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 100px;
}

.intro {
  font-family: var(--main-font);
  margin: 20px;
}

.add-todo {
  width: 100%;
}

.user-list {
  margin-top: 10px;
}

